.TopPreview {
    display:flex;
    text-align: left;
    padding-top:1rem;
    cursor: pointer;
    &__img {
        width:10rem;
        height:10rem;
    }
    &__content {
        margin-left: 1rem;
    }
    
    &__title {
        font-size: 2rem;
    }
    &__date {
        font-size: 1.2rem;
    }
    &__platforms {
        font-size: 1.2rem;
    }
}